<template>
  <v-container
    fluid
    grid-list-xl>

    <v-layout
      row
      wrap>
      <v-flex
        xs12
        style="text-align: left">
        <h1 class="headline">
          {{ title }}
        </h1>
      </v-flex>
    </v-layout>

    <v-layout
      row>

      <v-flex
        xs4
        md3
        lg2>
        <v-card
          style="
            height: calc(100vh - 180px);
            overflow: auto;">
          <v-card-title class="headline">
            Parameters
          </v-card-title>
          <v-card-text>
            <div
              v-for="(param, i) of sliders"
              :key="i"
              style="text-align: left"
              column>
              {{ param.label }} =
              {{ param.decimal ? param.value.toFixed(param.decimal) : param.value }}
              <v-slider
                :step="param.interval"
                :max="param.max"
                v-model="param.value"
                @callback="changeGraph()"/>
            </div>
          </v-card-text>
        </v-card>
      </v-flex>

      <v-flex
        xs8
        md9
        lg10>
        <v-card
          style="
            height: calc(100vh - 180px);
            overflow: auto;">
          <v-card-title class="headline">
            Graphs
          </v-card-title>
          <v-card-text>
            <div id="epi-charts"/>
          </v-card-text>
        </v-card>
      </v-flex>

    </v-layout>

  </v-container>
</template>

<style>
.chart {
  height: 300px;
  max-width: 600px;
}
</style>

<script>
import util from '../modules/util'
import ChartWidget from '../modules/chart-widget'
import { epiModels } from '../modules/epi-models'
import config from '../config'

import $ from 'jquery'
import _ from 'lodash'

export default {
  data() {
    return {
      title: config.title,
      sliders: [],
      modelName: '',
      modelNames: []
    }
  },
  watch: {
    sliders: {
      handler() {
        this.changeGraph()
      },
      deep: true
    }
  },
  async mounted() {
    this.modelNames = _.map(epiModels, e => e.name)
    this.modelName = epiModels[0].name
    this.changeModel()
  },
  methods: {
    changeModel() {
      let epiModel = _.find(epiModels, e => e.name === this.modelName)
      this.model = new epiModel.Class()
      this.charts = [
        {
          title: 'COMPARTMENTS',
          divTag: 'compartment-chart',
          keys: _.keys(this.model.compartment),
          xlabel: 'time (years)'
        },
        {
          title: 'STACKED-COMPARTMENTS',
          divTag: 'stacked-compartment-chart',
          keys: _.keys(this.model.compartment),
          xlabel: 'time (years)',
          isStacked: true
        },
        {
          title: 'LOW COMPARTMENTS',
          divTag: 'low-compartment-chart',
          keys: ['diagnosed', 'treated', 'susceptibleAgain'],
          xlabel: 'time (years)'
        },
        {
          title: 'STACKED-LOW-COMPARTMENTS',
          divTag: 'stacked-low-compartment-chart',
          keys: ['diagnosed', 'treated', 'susceptibleAgain'],
          xlabel: 'time (years)',
          isStacked: true
        },
        {
          title: 'EPI-COMPARTMENTS',
          divTag: 'epi-compartment-chart',
          keys: ['prevalence', 'incidence', 'mortality'],
          xlabel: 'time (years)'
        },
        {
          title: 'STACKED-EPI-COMPARTMENTS',
          divTag: 'stacked-epi-compartment-chart',
          keys: ['prevalence', 'incidence', 'mortality'],
          xlabel: 'time (years)',
          isStacked: true
        }
      ]
      util.copyArray(this.sliders, this.model.guiParams)

      this.graphWidgets = {}
      let $div = $('#epi-charts').empty()
      for (let chart of this.charts) {
        let id = chart.divTag
        $div.append(
          $('<div>')
            .attr('id', id)
            .addClass('chart')
        )
        let chartWidget = new ChartWidget(`#${id}`)
        chartWidget.setTitle(chart.title)
        chartWidget.setXLabel(chart.xlabel)
        chartWidget.setYLabel('')
        if (chart.isStacked) {
          for (let [i, key] of chart.keys.entries()) {
            console.log('changeModel', i, key)
            chartWidget.addDataset(key)
            _.last(chartWidget.getDatasets()).fill = true
          }
        } else {
          for (let key of chart.keys) {
            chartWidget.addDataset(key)
          }
        }
        this.graphWidgets[chart.divTag] = chartWidget
      }
    },
    changeGraph() {
      this.model.importGuiParams(this.sliders)
      let dStepInDays = 2
      let timeInDays = this.model.param.years * 365
      let nStep = timeInDays / dStepInDays
      this.model.run(nStep, dStepInDays)
      let times = _.clone(this.model.times)
      times = _.map(this.model.times, t => t / 365)

      console.log('changeGraph times', this.model.times)
      for (let chart of this.charts) {
        if (chart.isStacked) {
          let soln
          let yMax = 0
          for (let [i, key] of _.reverse(chart.keys.entries())) {
            if (_.isNil(soln)) {
              soln = this.model.solution[key]
            } else {
              soln = _.map(
                _.zip(soln, this.model.solution[key]),
                v => v[0] + v[1]
              )
            }
            // this.graphWidgets[chart.divTag].updateDataset(i, times, soln)
            let y = _.last(soln)
            this.graphWidgets[chart.divTag].updateDataset(i, [0, 1], [y, y])
            if (y > yMax) {
              yMax = y
            }
            console.log('changeGraph stacked', i, key, yMax, _.takeRight(soln, 5))
          }
          this.graphWidgets[chart.divTag].setXMax(1)
          this.graphWidgets[chart.divTag].setYMax(yMax)
        } else {
          for (let [i, key] of chart.keys.entries()) {
            console.log('changeGraph line', key, _.takeRight(this.model.solution[key], 5))
            this.graphWidgets[chart.divTag].updateDataset(
              i,
              times,
              this.model.solution[key]
            )
            this.graphWidgets[chart.divTag].setXMax(_.max(times))
          }
        }
      }
    }
  }
}
</script>



